import { initializeApp } from "firebase/app";
import { getFirestore } from 'firebase/firestore/lite';
import { browserSessionPersistence, getAuth, setPersistence } from "firebase/auth";
const firebaseConfig = {
  apiKey: process.env.API_KEY,
  authDomain: process.env.AUTH_DOMAIN,
  projectId: process.env.PROJECT_ID,
  storageBucket: process.env.STORAGE_BUCKET,
  messagingSenderId: process.env.MESSAGING_SENDER_ID,
  appId: process.env.APP_ID,
};

const app = initializeApp(firebaseConfig);

export const db = getFirestore(app);
export const auth = getAuth(app);

setPersistence(auth, browserSessionPersistence);


console.log({
  'process.env.API_KEY': process.env.API_KEY,
  'process.env.AUTH_DOMAIN': process.env.AUTH_DOMAIN,
  'process.env.PROJECT_ID': process.env.PROJECT_ID,
  'process.env.STORAGE_BUCKET': process.env.STORAGE_BUCKET,
  'process.env.MESSAGING_SENDER_ID': process.env.MESSAGING_SENDER_ID,
  'process.env.APP_ID': process.env.APP_ID,
});